@use 'base/variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$textFont;
    font-size: 1.6rem;
    min-height: 100%;
}
.container {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
.container-sm {
    max-width: 70rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
span {
    font-weight: 700;
}
.app {
    padding: 3rem 0;
}
